:root{
    /* colors */
    --dark-grey: #24232C;
    --grey: #817D92;
    --almost-white: #E6E5EA;
    --very-dark-grey: #18171F;
    --neon-green: #A4FFAF;
    --red: #F64A4A;
    --orange: #FB7C58;
    --yellow: #F8CD65;
}

* {
  transition: all .1s;
}

body{
    font-family: 'JetBrains Mono', sans-serif ;
    color: var(--almost-white);
    background: linear-gradient(180deg, #14131B 0%, #08070B 100%);
    margin: 0;
}

.hidden {
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: 1px;}

.container {
    min-width: 320px;
    max-width: 343px;
    padding-left: 16px;
    padding-right: 16px;
    margin-left: auto;
    margin-right: auto;
}

.section-generator {
    padding-top: 64px;
    padding-bottom: 63px;
}

.title{
  margin-bottom: 16px;
  color: var(--grey);
  text-align: center;
   display: flex;
   padding-left: 90px;
   
}
.app-name {
    margin-top: 0px;
    margin-bottom: 0px;
    margin-right: 40px;
    font-size: 16px;
    font-weight: 700;

}
.password {
    background-color: var(--dark-grey);
    padding: 17px 16px 15px 16px;
    display: flex;

    margin-bottom: 16px;
}

.text-password{
    margin-top: 0;
    margin-bottom: 0;
    margin-right: 100px;
    font-size: 24px;
    font-weight: 700;
    
}

.copied {
color: var(--neon-green);
}
.btn-copy {
    height: 20px;
}
.generator-form {
    background-color: var(--dark-grey);
    padding: 16px;
}
.selected-length{
    display: flex;
    gap: 128px;
    align-items: center;
    margin-bottom: 23px;
}
.text-length {
    margin-top: 0px;
    margin-bottom: 0px;
    font-weight: 700;
}
.number-length {
    font-size: 24px;
    font-weight: 700;
    color: var(--neon-green);
}


.input-length {
    appearance: none;
    width: 100%;
    height: 8px;
    background: var(--very-dark-grey);
    background-image: linear-gradient(var(--neon-green), var(--neon-green));
    background-size: 0% 100%;
    background-repeat: no-repeat;
    margin-bottom: 42px;
  }
  
  [dir="rtl"] .input-length {
    background: var(--neon-green);
    background-image: linear-gradient(#fff, #fff);
    background-size: 30% 100%;
    background-repeat: no-repeat;
  }
  
  /* Input Thumb */
  .input-length::-webkit-slider-thumb {
    appearance: none;
    height: 28px;
    width: 28px;
    border-radius: 50%;
    background: var(--almost-white);
    cursor: pointer;
    transition: background .3s ease-in-out;
  }
  
  .input-length::-moz-range-thumb {
    appearance: none;
    height: 28px;
    width: 28px;
    border-radius: 50%;
    background: var(--almost-white);
    cursor: pointer;
    transition: background .3s ease-in-out;
  }
  
  .input-length::-ms-thumb {
    appearance: none;
    height: 28px;
    width: 28px;
    border-radius: 50%;
    background: var(--almost-white);
    cursor: pointer;
    transition: background .3s ease-in-out;
  }
  
  .input-length::-webkit-slider-thumb:hover {
    background: var(--dark-grey);
    border: 2px solid var(--neon-green);
  }
  
  .input-length::-moz-range-thumb:hover {
    background: var(--dark-grey);
    border: 2px solid var(--neon-green);
  }
  
  .input-length::-ms-thumb:hover {
    background: var(--dark-grey);
    border: 2px solid var(--neon-green);
  }
  
  /* Input Track */
  .input-length::-webkit-slider-runnable-track  {
    appearance: none;
    box-shadow: none;
    border: none;
    background: transparent;
  }
  
  .input-length::-moz-range-track {
    appearance: none;
    box-shadow: none;
    border: none;
    background: transparent;
  }
  
  ::-ms-track {
    appearance: none;
    box-shadow: none;
    border: none;
    background: transparent;
  }


  
  .checkboxes{
    display: flex;
    flex-direction: column;
    gap: 16px;
    margin-bottom: 32px;
   
  }

  .checkbox{
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
    gap: 20px;
    font-weight: 700;
  }

 



