.btn-generate {

display: block;
width: 100%;
height: 56px;
background-color: var(--neon-green);
color: var(--dark-grey);
cursor: pointer;
font-weight: 700;
letter-spacing: 0.050em;
border: none;

}
.btn-generate:hover{
  background-color: transparent;
  border:  2px solid var(--Neon-Green, #A4FFAF);
  color: var(--neon-green);  
}
.btn-generate:focus{
  background-color: transparent;
  border:  2px solid var(--Neon-Green, #A4FFAF);
  color: var(--neon-green);  
}


.slider-btn {
  position: relative;

  }

  .slider-btn::before{
    bottom: 0;
    content: '';
    display: inline-block;
    margin: auto;
    background-color: var(--grey);
    border-radius: 50px;
    top: 0;
    height: 15px;
    width: 30px;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.493);
  }


  /* circle */
  .slider-btn::after {
    bottom: 0;
    content: '';
    display: inline-block;
    margin: auto;
    background-color: var(--neon-green);
    border-radius: 50%;
    position: absolute;
    top: -5px;
    left: 5px;
    height: 10px;
  
    transition: .4s;
    width: 10px;
  }


.toggle-checkbox {
  /* hide the "default" checkbox/appearance */
  display: none;}

  .toggle-checkbox:checked ~ .slider-btn:before {
    background-color: var(--almost-white);
  }
  
  .toggle-checkbox:checked ~ .slider-btn:after {
    background-color: maroon;
    transform: translateX(10px);
  }
  

  .body-ligth{
    background: linear-gradient(180deg, var(--grey), var(--grey) 100%);
